.reg_container{
    width: 95%;
    margin: 2% auto;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.reg_background{
    background-color: #fff;
    box-shadow: 0px 8px 28px rgba(78, 78, 78, 0.2);
    border-radius: 10px;
    padding: 2% 5%;
    display: flex;
    flex-direction: column !important;
    
}
.reg_header_name{
    margin-bottom: 20px;
    text-align: center;
    white-space: nowrap;
    
}
.reg_form{
    min-height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    
}
.reg_grp_button{
    display: flex;
    align-items: center;
    justify-content: center;   
}